.editable-list__form {
  align-items: center;
  display: flex; }
  .editable-list__form .tyk-form-group {
    margin-bottom: 0; }
  .editable-list__form--sticked {
    margin-left: 0; }
    .editable-list__form--sticked button {
      border-top-left-radius: 0px;
      border-bottom-left-radius: 0px;
      border-top-right-radius: 3px;
      border-bottom-right-radius: 3px;
      padding-bottom: 11px;
      padding: 10px 15px 11px 15px; }
  .editable-list__form span.inline {
    margin-top: 0; }
  .editable-list__form > span {
    margin-left: 15px; }

.editable-list__header {
  border-bottom: 1px solid #D2D2D2;
  color: #000018;
  font-size: 11px;
  padding-bottom: 15px;
  text-transform: uppercase; }
  .editable-list__header .tyk-row {
    margin: 0 -10px 0 0; }

.editable-list-items {
  overflow: hidden;
  list-style: none;
  padding: 0 0 20px 0; }
  .editable-list-items .edit-mode-wrapper.table button {
    margin-top: 23px; }
  .editable-list-items .tyk-row {
    margin: 0 -10px 0 0; }
  .editable-list-items button[disabled] {
    cursor: not-allowed; }
  .editable-list-items.editable-list-items--inline {
    align-items: center;
    color: #000018;
    display: flex;
    flex-wrap: wrap;
    padding: 0; }
  .editable-list-items li {
    border-top: 1px solid #D2D2D2;
    transition: all .2s linear; }
    .editable-list-items li.editable-list-item--inline {
      background-color: #E1E1E1;
      border-radius: 25px;
      border: none;
      color: white;
      float: left;
      margin-bottom: 10px;
      margin-right: 15px;
      padding: 0 15px;
      white-space: nowrap; }
      .editable-list-items li.editable-list-item--inline .editable-list-item__text {
        color: #000018;
        padding: 5px 0; }
      .editable-list-items li.editable-list-item--inline .editable-list-action__button.on-edit {
        margin-top: 0; }
    .editable-list-items li:first-child {
      border-top: none; }
    .editable-list-items li .editable-list-item__text {
      display: flex;
      padding: 10px 0; }
      .editable-list-items li .editable-list-item__text > span {
        flex-grow: 1; }
      .editable-list-items li .editable-list-item__text > div:first-child {
        flex-grow: 1; }
        .editable-list-items li .editable-list-item__text > div:first-child .error-message {
          margin: 0; }
    .editable-list-items li .editable-list-items {
      overflow: hidden;
      padding-bottom: 10px; }
      .editable-list-items li .editable-list-items.editable-list-item--inline {
        padding: 0; }
    .editable-list-items li .editable-list {
      border-top: 5px solid #D2D2D2;
      background: #F0F0F0;
      padding: 10px 15px; }
  .editable-list-items .editable-list-item__text {
    display: block; }
  .editable-list-items .editable-list-action__button {
    border: none;
    background: transparent; }
    .editable-list-items .editable-list-action__button.on-edit {
      margin-left: 10px; }
  .editable-list-items .editable-list-item-controls {
    flex-grow: 0;
    text-align: right; }
